import React, { useState } from 'react';
import ReactDatePicker from 'react-datepicker';
import {
  NavLink,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Label,
} from 'reactstrap';
import { API_URI } from '../../../../constants/defaultValues';
import IntlMessages from '../../../../helpers/IntlMessages';
import { getDateWithFormat, log } from '../../../../helpers/Utils';

const DownloadReports = () => {
  const [values, setValues] = useState({
    from: getDateWithFormat(),
    to: getDateWithFormat(),
  });

  const handleDateChange = (value, key) => {
    log(value);
    setValues({
      ...values,
      [key]: getDateWithFormat(value),
    });
  };

  return (
    <Card>
      <CardBody>
        <CardTitle>
          <IntlMessages id="dashboard.download-report" />
        </CardTitle>
        <FormGroup>
          <Label>
            <IntlMessages id="dashboard.download-report-from" />
          </Label>

          <ReactDatePicker
            value={values.from}
            onChange={(newDate) => handleDateChange(newDate, 'from')}
          />
        </FormGroup>
        <FormGroup>
          <Label>
            <IntlMessages id="dashboard.download-report-to" />
          </Label>

          <ReactDatePicker
            value={values.to}
            onChange={(newDate) => handleDateChange(newDate, 'to')}
          />
        </FormGroup>

        <FormGroup className="d-flex justify-content-end">
          <NavLink
            size="sm"
            className="btn-shadow btn btn-primary"
            download
            href={`${API_URI}/dashboard/download-client-service-report-datewise?from=${values.from}&to=${values.to}`}
            target="_blank"
          >
            <IntlMessages id="dashboard.download-reports-button" />
          </NavLink>
        </FormGroup>
      </CardBody>
    </Card>
  );
};

export default DownloadReports;
