/* eslint-disable camelcase */
import React, { useEffect } from 'react';
import { Card, CardBody, CardTitle, Row } from 'reactstrap';
import { connect } from 'react-redux';

import LineChart from './components/vistors-chart';
import PieChart from './components/referral-chart';
import { Colxx, Separator } from '../../../components/common/CustomBootstrap';
import BreadcrumbContainer from '../../../containers/navs/Breadcrumb';
import IntlMessages from '../../../helpers/IntlMessages';
import DataCards from './components/data-cards';
import { log } from '../../../helpers/Utils';
import useRequest from '../../../hooks/use-request';
import { ThemeColors } from '../../../helpers/ThemeColors';

import 'react-datepicker/dist/react-datepicker.css';
import DownloadReports from './components/dowload-reports';

const colors = ThemeColors();

const INIT_CHART_DATA = {
  labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
  datasets: [
    {
      label: '',
      data: [54, 63, 60, 65, 50, 68, 60],
      borderColor: colors.themeColor1,
      pointBackgroundColor: colors.foregroundColor,
      pointBorderColor: colors.themeColor1,
      pointHoverBackgroundColor: colors.themeColor1,
      pointHoverBorderColor: colors.foregroundColor,
      pointRadius: 6,
      pointBorderWidth: 2,
      pointHoverRadius: 8,
      fill: false,
    },
  ],
};

const Dashboard = ({ match, accessToken, currentUser }) => {
  const { is_superadmin } = currentUser;

  const [getCard, cardLoading, cardError, cardsData] = useRequest({
    url: '/dashboard/get-card-info',
    headers: {
      Authorization: accessToken,
    },
    initialValue: [
      {
        title: 'dashboards.no-of-clients',
        icon: 'iconsminds-clock',
        value: 0,
      },
      {
        title: 'dashboards.no-of-services',
        icon: 'iconsminds-basket-coins',
        value: 0,
      },
      {
        title: 'dashboards.no-of-visits',
        icon: 'iconsminds-arrow-refresh',
        value: 0,
      },
    ],
  });

  const [
    getLineChart,
    chartLineLoading,
    chartLineError,
    lineChartData,
  ] = useRequest({
    url: '/dashboard/get-client-visits',
    headers: {
      Authorization: accessToken,
    },
    initialValue: INIT_CHART_DATA,
    nextSuccess: (responseData, setData) => {
      setData({
        ...INIT_CHART_DATA,
        labels: responseData.labels,
        datasets: [{ ...INIT_CHART_DATA.datasets[0], data: responseData.data }],
      });
    },
  });

  const [
    getPieChart,
    chartPieLoading,
    chartPieError,
    pieChartData,
  ] = useRequest({
    url: '/dashboard/get-referral-pie-chart',
    headers: {
      Authorization: accessToken,
    },
    initialValue: INIT_CHART_DATA,
    nextSuccess: (responseData, setData) => {
      setData({
        ...INIT_CHART_DATA,
        labels: responseData.labels,
        datasets: [{ ...INIT_CHART_DATA.datasets[0], data: responseData.data }],
      });
    },
  });

  useEffect(() => {
    getCard();
    getLineChart();
    getPieChart();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (cardLoading || chartLineLoading || chartPieLoading)
    return <div className="loading" />;

  if (chartLineError || chartPieError || cardError)
    log({ chartLineError, chartPieError, cardError });

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <BreadcrumbContainer heading="menu.dashboard" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row>
        <Colxx xxs="12" md="8" className="mb-4">
          <Card className="h-100">
            <CardBody>
              <CardTitle>
                <IntlMessages id="dashboard.client-activity-data" />
              </CardTitle>
              <div className="dashboard-line-chart my-4">
                <LineChart data={lineChartData} />
              </div>
            </CardBody>
          </Card>
        </Colxx>

        <Colxx xxs="12" md="4" className="mb-4">
          {is_superadmin && <DownloadReports />}
        </Colxx>
      </Row>

      <Row>
        <Colxx xxs="12" md="8" className="mb-4">
          <Card className="h-100">
            <CardBody>
              <CardTitle>
                <IntlMessages id="dashboard.client-referral-data" />
              </CardTitle>
              <div className="dashboard-line-chart my-4">
                <PieChart data={pieChartData} />
              </div>
            </CardBody>
          </Card>
        </Colxx>
      </Row>

      <Row>
        <Colxx lg="12" xl="8">
          <DataCards data={cardsData} />
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { accessToken, currentUser } = authUser;
  return { accessToken, currentUser };
};

export default connect(mapStateToProps, {})(Dashboard);
